<template>
  <div class="wrap">
    <div class="hearer">
      <p class="title">메모장</p>
      <button type="button" class="btn btn--block back" v-if="mode !== 'LIST'" @click="mode = 'LIST'"><span class="blind">list 이동</span></button>
    </div><!-- // header -->
    <div class="contents">
      <div class="memoBox" v-if="mode ==='LIST'">
        <ul class="memoList">
          <li v-for="m in memos" :key="m">
            <table>
              <colgroup>  
                      <col style="width:40px">
                      <col style="width:40%">
                      <col>
              </colgroup>
              <tr>
                <td class="memo--no">{{m.id}}</td>
                <td class="memo--title" @click="open(m.id)">{{m.content}}</td>
                <td class="memo--time">{{(m.regData)}}</td>
              </tr>
            </table>
          </li>
        </ul>
      </div><!--// v-if -->
      <div class="memoWrite" v-else>
        <div class="textForm">
          <textarea v-model="memo.content"></textarea>
        </div>
        <div class="btns--wrap btns--wrap__bottom flex">
          <!-- <buttom type="button" class="btn btn--block save" @click="save()">save</buttom> -->
          <buttom type="button" class="btn btn--block save" @click="save()" v-bind:class="{'full' : mode === 'write'}">save</buttom><!-- mode가 write 일때 full 클래스 추가 -->
          <buttom type="button" class="btn btn--block cancel" @click="cancel()" v-if="mode === 'write'">cancel</buttom>
          <buttom type="button" class="btn btn--block remove" @click="remove()" v-if="mode === 'edit'">delete</buttom>
        </div>
      </div><!--// else -->
      <div class="btns--wrap">
        <button type="button" class="btn add" @click="write()" v-if="mode === 'LIST'"><span class="blind">메모 추가</span></button>
      </div>
    </div><!-- // contents -->
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  data(){
    return{
      mode: 'LIST',
      memo: {
        id: null,
        content: null,
        regData: null
      },
      memos:[
        // {
        //   id: 1,
        //   content: '할일',
        //   regData: new Date()
        // },
        // {
        //   id: 2,
        //   content: 'todo',
        //   regData: new Date()
        // },
        // {
        //   id: 3,
        //   content: 'study',
        //   regData: new Date()
        // },
        // {
        //   id: 4,
        //   content: 'vue',
        //   regData: new Date()
        // },
      ],
    }
  }, // data
  methods:{
    renew(val){ // 
      return JSON.parse(JSON.stringify(val));
    },
    open(id){ // 매개변수로 id를 받고 
      // console.log(id)
      for(var i in this.memos){ // memos 를 반복 돌면서
        if(this.memos[i].id === id){ // id 가 같은 메모를 찾아서
          // this.memo = this.memos[i];
          this.memo = this.renew(this.memos[i]);
          break;
        }
      }
      this.mode  ="edit"
    },
    write(){
      this.mode = "write"
      this.memo = {
        id: null,
        content: null,
        regData: null
      };
    },
    cancel(){
      this.mode = "LIST"
    },
    remove(){
      if(confirm('삭제ㄱㄱ?')){
        for(var i in this.memos){
          if(this.memos[i].id === this.memo.id){
            // this.memos[i].content = this.memo.content
            this.memos.splice(i, 1);
            break;
          }
        }
        localStorage.setItem('momos', JSON.stringify(this.memos));
        this.mode = "LIST"
      }
    },
    save(){
      // if(this.memo.content !== null){ // content 값이 있을때만!
        
      // } 
      let id = this.memos.length + 1;
      if(this.mode === 'write'){ 
        this.memos.push({
          id: id,
          content: this.memo.content,
          regData: new Date()
        });
      } else if(this.mode ==='edit'){
        for(var i in this.memos){
          if(this.memos[i].id === this.memo.id){
            // this.memos[i].content = this.memo.content
            this.memos[i] = this.renew(this.memo);
            break;
          }
        }
      }
      this.mode = "LIST";
      localStorage.setItem('momos', JSON.stringify(this.memos)); // 로켈 스토리지에 json 형태로 저장

      // if(this.memo.content !== null){ // content 값이 있을때만!
      //   console.log('Y')
      //   this.memos.push({
      //       id: id,
      //       content: this.memo.content,
      //       regData: new Date()
      //   });
      //   this.memo.content = null;
      //   this.mode = "LIST"
      //   localStorage.setItem('momos', JSON.stringify(this.memos)); // 로켈 스토리지에 json 형태로 저장
      // } else {
      //   console.log('N')
      // }
      
    },
    newTime(date){
      let hour = date.getHours()      
      let minutes = date.getMinutes()
      let fullDate = `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
      return `${fullDate} ${hour}:${minutes}`
    },
  },
  created(){
    let memos = localStorage.getItem('momos');
    if(memos){
      this.memos = JSON.parse(memos);
    }
  },
  // components: {
  //   HelloWorld
  // }
}
</script>

